import React, { useState } from "react";
import styled from "styled-components";
// HELPER PACKAGES
import ReactPlayer from "react-player/lazy";
import Photogrid from "react-facebook-photo-grid";
import Translate from "../../shared/Translate";
import FbImageLibrary from "react-fb-image-grid";
import { i18n } from "src/i18n";
import MainSplideStyles from "../styles/MainSplideStyles";
// PACKAGES
import { Splide, SplideSlide } from "@splidejs/react-splide";
import "@splidejs/react-splide/css";
export default function Main({ entity, data }) {
  const language = localStorage.getItem("language");

  return (
    <MainLayout>
      {entity["photos"]?.length > 0 && (
        <>
          <MainSplideStyles>
            <Splide
              options={{
                perPage: 1,
                perMove: 1,
                pagination: true,
                wheel: false,
                direction: language === "ar" ? "rtl" : "ltr",
              }}
              className="splide"
            >
              {data?.map((post, index) => {
                return (
                  <SplideSlide
                    key={index}
                    className="horizontalPlayer"
                    style={{
                      background:
                        "linear-gradient(115.19deg, #2B2840 28.27%, rgba(43, 40, 64, 0) 68.81%), linear-gradient(0deg, rgba(43, 40, 64, 0.7), rgba(43, 40, 64, 0.7)), url(" +
                        post +
                        ")",
                    }}
                  >
                    <img style={{ height: 507 }} src={post} />
                  </SplideSlide>
                );
              })}
            </Splide>
          </MainSplideStyles>
          <hr />
        </>
      )}

      {entity["videos"]?.length > 0 && (
        <div>
          <VideoPlayersLayout>
            {entity["videos"].map((vid) => {
              return (
                <ReactPlayer
                  width={900}
                  key={vid._id}
                  url={vid["downloadUrl"]}
                  loop={true}
                  controls={true}
                  className="video__player"
                />
              );
            })}
          </VideoPlayersLayout>
          <hr />
        </div>
      )}
      <div className="description__detaillEvenement">
        {Translate.Trans("description", entity)}
        <hr />
      </div>
      {entity["link"] ? (
        <div className="detail__author">
          <h4>
            <a href={entity.link}>{i18n(`entities.mediatique.visitLink`)}</a>
          </h4>
          <hr />
        </div>
      ) : null}
      {entity["attachements"]?.length > 0 ? (
        <div>
          <a
            href={entity?.attachements[0].downloadUrl}
            download
            style={{ color: "#9a0000" }}
          >
            {i18n("telecharger")}
          </a>
        </div>
      ) : null}
    </MainLayout>
  );
}

const MainLayout = styled.section`
  margin: 2rem 0;
  p {
    text-align: justify;
    color: var(--gray2);
    font-family: "Proxima Nova";
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 25px;
  }

  .video__player {
    max-width: 900 !important;
    max-height: 350px !important;
    flex: 1 !important;
  }
  .hTKJIQ {
    margin: -5rem 0 3rem 0 !important;
  }
  @media (max-width: 767px) {
    width: 100%;
    padding: 0 0.5rem;
  }
`;
const VideoPlayersLayout = styled.section`
  margin-top: 2rem;
  margin: 1rem 0;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  audio {
    height: 70px !important;
  }

  @media (max-width: 767px) {
    video {
      width: 90% !important;
    }
  }
`;
const PhotoPlayersLayout = styled.section`
  margin-top: 1rem;

  .gridSection {
    width: 100%;
    display: block;
    &:before {
      display: block;
      font-family: "Proxima Nova";
      font-style: normal;
      font-size: 1rem;
      margin-bottom: 1rem;
    }
  }

  img {
    width: 100%;
  }
`;
